import tuiIcon from "@/components/icon/icon"
import tuiModal from "@/components/modal/modal"
export default {
	components: {
		tuiIcon,
		tuiModal
	},
	data() {
		return {
			secret: {},
			userInfo: '',
			showModal: true,
			// 订单类型
			orderList: [{
					text: '未付款',
					icon: "fukuan",
					id: "0",
					num: 0
				},
				{
					text: '已付款',
					icon: "fahuo",
					id: "1",
					num: 0
				},
				{
					text: '已发货',
					icon: "shouhuo",
					id: "3",
					num: 0
				},
				{
					text: '已收货',
					icon: "pingjia",
					id: "4",
					num: 0
				},
				{
					text: '退款',
					icon: "tuihuo",
					id: "8",
					num: 0
				}
			],
			// 工具栏列表
			mytoolbarList: [{
					url: '../../user/address/address',
					text: '收货地址',
				},
				
				{
					url: '../../user/keep/keep',
					text: '我的收藏'
				},
				{
					url: '',
					text: '团购专享'
				},
				{
					url: '',
					text: '关于我们'
				},
			],
			// 购物车数量
			count: 0
		}
	},
	onLoad() {
		uni.getStorage({
			key: 'user',
			success: (res) => {
				this.secret = res.data;
			}
		})

	},
	onShow() {
		this.getcount();
		uni.getStorage({
			key: 'cart',
			success: (res) => {
				this.count = res.data;
			}
		})

		uni.getStorage({
			key: 'userInfo',
			success: (res) => {
				this.userInfo = res.data;
				if (!this.userInfo) {
					this.showModal = true
				} else {
					this.showModal = false;
				}
			}
		})

	},
	methods: {
		getuserinfo() {
			// wx登录
			uni.login({
				success: (res) => {
					if (res.code) {
						var code = res.code
						uni.getUserInfo({
							success: (res) => {
								this.userInfo = res.userInfo;
								this.showModal = false;
								let data = {
									encryptedData: res.encryptedData,
									iv: res.iv,
									sessionKey: this.secret.sessionKey,
									secret: this.secret.secret,
									timestamp: this.secret.timestamp,
								}
								this.$xm.post('/Index/getUnionid', data, (res) => {
									this.secret.unionid = res.unionid
									uni.setStorage({
										key: 'user',
										data: this.secret,
										success: (res) => {
											console.log(res);
										}
									});
								})
								uni.setStorage({
									key: 'userInfo',
									data: res.userInfo,
									success: (res) => {
										console.log(res);
									}
								});

							},
							fail: res => {
								// 获取失败的去引导用户授权 
							}
						})

					} else {}
				}
			})
		},
		toOrderList(index) {
			if (!index) {
				index = "s"
			}
			console.log(index)
			uni.setStorageSync('tbIndex', index);
			uni.navigateTo({
				url: '../../user/order_list/order_list?tbIndex=' + index
			})
		},
		// 获取订单shuliang
		getcount() {				
			this.$xm.post('/Mine/index', '', (res) => {
				console.log(res);
				let result = res.order_state;
				for(let i in result){
					
					if(result[i].order_state==this.orderList[i].id){
						this.orderList[i].num=result[i].state
					}
				}


			})
		},
		toCart() {
			uni.switchTab({
				url: '../cart/cart'
			})
		},
		toPage(url) {
			console.log(url);
			if (!url) {
				uni.showToast({
					title: '此内容正在开发中',
					icon: "none"
				});
				return;
			}
			uni.navigateTo({
				url: url
			})
		},
		close() {
			this.showModal = false;
		}
	}
}
